.our-clients {
    position: relative;
    margin-top: 113px;
    background-color: $blue_04;
    clip-path: inset(0 0 0 0 round 200px 0 0 0);
}

.our-clients__container {
    @include container;
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-template-rows: auto auto;
    padding: 90px 0 125px 0;
}

.our-clients__title {
    font-size: 36px;
    line-height: 46px;
    font-weight: 900;
    letter-spacing: 1.5px;
    color: $white;
    margin: 0;
    margin-top: 2px;
    text-transform: capitalize;
}

.our-clients__text {
    font-size:  16px;
    line-height: 26px;
    letter-spacing: 0.8px;
    color: transparentize($color: $white, $amount: 0.4);
    margin-top: 4px;
    padding: 0 80px 0 5px;
}

.our-clients__table {
    grid-column: 1/3;
    margin-top: 110px;
    border-collapse: collapse;

    &-string {
        border-bottom: 2px solid transparentize($color: $white, $amount: 0.6);

        &:last-of-type {
            border-bottom: none;

            .our-clients__table-column:last-of-type {
                a {
                    text-transform: capitalize;
                }
            }
        }
    }

    &-column {
        height: 126px;
        border-right: 2px solid transparentize($color: $white, $amount: 0.6);
        text-align: center; 
        
        &:last-of-type {
            border-right: none;
        }

        a {
            display: flex;
            font-size: 20px;
            line-height: 46px;
            font-weight: 900;
            letter-spacing: 0.4px;
            height: 100%;
            justify-content: center;
            align-items: center;
            color: $white;
            text-decoration: none;
            text-transform: uppercase;
        }

        &:hover,
        &--hover {
            background-color: transparentize($color: $blue_01, $amount: 0.8);
        }
    }
}