.nav {
    display: flex;
    align-items: center;
    // outline: 1px solid green;
    padding: 0 83px;
    margin: 0 auto;
}

.nav__logo {
    margin-right: auto;

    &-svg {
        fill: $blue_02;
    }
}

.nav__list {
    display: none;

    &--active {
        display: flex;
    }
}

.nav__button-menu {
    position: relative;
    width: 28px;
    height: 3px;
    background-color: $blue_03;
    // box-shadow: 0 6px 0 0 $blue_03, 0 12px 0 0 $blue_03;
    margin-top: -17px;
    border: none;
    cursor: pointer;

    &::after {
        content: "";
        position: absolute;
        top: 6px;
        left: 0;
        width: 28px;
        height: 3px;
        background-color: $blue_03;
    }

    &::before {
        content: "";
        position: absolute;
        top: 12px;
        left: 0;
        width: 28px;
        height: 3px;
        background-color: $blue_03;
    }
}