.footer-main {
    background-color: $blue_04;
    margin-top: -1px;
    padding-top: 5px;
}

.footer__container {
    position: relative;
    width: 93%;
    height: 100%;
    margin-top: -2px;
    padding: 88px 0;
    background-color: $blue_02;
    clip-path: inset(0 0 0 0 round 0 200px 0 0);

    &::after {
        content: "";
        position: absolute;
        top: 390px;
        left: 0;
        width: 84.5%;
        height: 1px;
        background-color: transparentize($color: $white, $amount: 0.6);
    }
}

.footer__team {
    @include container;
    padding-left: 100px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;

    &-title {
        font-size: 36px;
        line-height: 46px;
        font-weight: 900;
        letter-spacing: 1.7px;
        color: $white;
        margin: 0;
    }

    &-text {
        font-size: 16px;
        line-height: 26px;
        letter-spacing: 0.7px;
        color: transparentize($color: $white, $amount: 0.4);
        grid-column: 1/2;
        grid-row: 2/3;
    }

    &-button {
        font-family: "Gilroy";
        font-size: 16px;
        line-height: 26px;
        letter-spacing: 0.5px;
        text-align: center;
        width: 220px;
        height: 41px;
        margin-top: 75px;
        margin-left: 168px;
        padding-top: 15px;
        border: none;
        border-radius: 10px;
        color: $white;
        background-color: $blue_04;
        text-transform: capitalize;
        text-decoration: none;
        grid-row: 1/3;
        cursor: pointer;

        &:hover {
            background-color: $blue_01;
        }
    }
}

.footer__info {
    @include container;
    padding-left: 100px;
    margin-top: 158px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, auto);

}

.footer__info-logo {
    display: flex;
    align-items: center;
    margin-top: 23px;

    &-svg1 {
        fill: $blue_05;
    }

    &-svg2 {
        margin-left: 15px; 
    }
}

.footer__info-list {
    @include list-style;
}

.footer__info-item-title {
    font-size: 20px;
    line-height: 30px;
    letter-spacing: 1px;
    font-weight: 900;
    color: $white;

    span {
        text-transform: uppercase;
    }
}

.footer__info-item-discription {
    font-size: 16px;
    line-height: 26px;
    font-weight: 200;
    font-style: normal;
    letter-spacing: 0.6px;
    color: $white;
    margin: 0;
    text-decoration: none;

    &:hover {
        color: $blue_05;
    }
}

.footer-contacts {
    display: flex;
    flex-direction: column;
    grid-column: 1/2;
    grid-row: 2/3;

    &__address {
        position: relative;
        padding-left: 33px;
        margin-top: 32px;

        &::before {
            content: "A.";
            position: absolute;
            top: 0;
            left: -1px;
            font-size: 16px;
            font-weight: 900;
            color: $white;
        }
    }

    &__tel {
        position: relative;
        padding-left: 33px;
        margin-top: 20px;

        &::before {
            content: "T.";
            position: absolute;
            top: 0;
            left: -1px;
            font-size: 16px;
            font-weight: 900;
            color: $white;
        }
    }

    &__email {
        position: relative;
        padding-left: 33px;
        margin-top: 24px;

        &::before {
            content: "E.";
            position: absolute;
            top: 0;
            left: -1px;
            font-size: 16px;
            font-weight: 900;
            color: $white;
        }
    }
}

.footer-about-us {
    grid-column: 2/3;
    grid-row: 1/3;
    padding-left: 85px;
}

.footer-socials {
    grid-column: 3/4;
    grid-row: 1/3;
    padding-left: 10px;
}

.footer-about-us .footer__info-item,
.footer-socials .footer__info-item {
    text-transform: capitalize;

    &:nth-of-type(2) {
        padding-top: 12px;
    }

    &:nth-of-type(3) {
        padding-top: 16px;
    }

    &:nth-of-type(4) {
        padding-top: 15px;
    }

    &:nth-of-type(5) {
        padding-top: 16px;
    }
}

.footer__info-item-copyright {
    font-size: 16px;
    line-height: 26px;
    letter-spacing: 0.7px;
    padding-top: 48px;
    color: transparentize($color: $white, $amount: 0.6);
    grid-column: 1/-1;
} 