.services {
    @include container;
    margin-top: 192px;
    // outline: 1px solid red;
}

.services__slogan {
    width: 400px;
    font-size: 36px;
    line-height: 46px;
    font-weight: 900;
    letter-spacing: 1.9px;
    margin: 0;
    padding: 0;
    color: $blue_02;
}

.services__list {
    @include list-style;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    margin-top: -135px;
}

.services__item {
    width: 287px;
    height: 525px;
    padding: 0 45px 0 48px;
    margin-left: 90px;
    border-radius: 10px;
    box-shadow: 5px 20px 50px transparentize($color: $blue_00, $amount: 0.8);

    &-title {
        font-size: 20px;
        line-height: 30px;
        font-weight: 900;
        letter-spacing: 1px;
        margin: 0;
        margin-top: 48px;
        color: $blue_01;
        text-transform: capitalize;
    }

    &-text {
        font-size: 16px;
        line-height: 26px;
        letter-spacing: 0.8px;
        margin-top: 20px;
        color: transparentize($color: $blue_01, $amount: 0.4);
    }

    &-link {
        display: flex;
        align-items: center;
        text-decoration: none;

        &-text {
            position: relative;
            font-size: 14px;
            line-height: 30px;
            font-weight: 900;
            letter-spacing: 0.8px;
            color: $blue_04;
        }

        &-icon {
            margin-top: 5px;
            margin-left: 25px;
            fill: $blue_04;
        }

        &:hover {
            * {
                color: $blue_03;
                fill: $blue_03;
            }
        }
    }

    &:nth-of-type(1) {
        grid-column: 2/3;
        margin-bottom: 100px;

        .services__item-img {
            margin-top: -20px;
            margin-left: 20px;
        }

        .services__item-title {
            margin-top: 40px;
        }
    }

    &:nth-of-type(2n) {
        margin-left: 80px;
        margin-top: -330px;

        .services__item-img {
            margin-top: 50px;
            margin-left: 15px;
        }
    }

    &:nth-of-type(3) {

        .services__item-img {
            margin-top: 48px;
            margin-left: 37px;
        }
    }
}