.header {
    z-index: 1;
    padding-top: 40px;
    margin: 0 auto;

    &-main {
        min-height: 727px;
        background: linear-gradient(0deg, $blue_05_opacity 8.84%, $white 31.12%);
    }
}

.header__container {
    max-width: 1600px;
    position: relative;
    padding-top: 30px;
    margin: 0 auto;
}

.header__title {
    font-family: "Time New Roman", serif;
    font-size: 64px;
    line-height: 74px;
    font-weight: 900;
    letter-spacing: 0.1px;
    margin-left: 213px;
    color: $blue_01;
    text-transform: capitalize;
}

.header__heading {
    height: 250px;
    margin-top: 35px;
    background-color: $blue_04;
    background-image: url("../images/header-background.svg");
    background-repeat: no-repeat;
    background-position: 0 0;
    background-size: cover;

    &-container {
        @include container;
        text-align: center;
    }

    &-title {
        margin: 0;
        padding-top: 50px;
        font-size: 44px;
        line-height: 74px;
        letter-spacing: 1.5px;
        font-weight: 900;
        color: $white;
        text-transform: capitalize;
    }

    &-text {
        margin: 0;
        padding: 18px 80px 0 80px;
        font-size: 20px;
        line-height: 30px;
        letter-spacing: 0.8px;
        color: $white;
        opacity: 0.6;
    }
}

.header__slogan {
    font-size: 16px;
    line-height: 26px;
    letter-spacing: 0.8px;
    color: $blue_01;
    opacity: 0.6;
    margin-top: -30px;
    margin-left: 213px;
}

.header__link {
    display: block;
    width: 190px;
    font-family: "Gilroy";
    font-size: 16px;
    line-height: 26px;
    letter-spacing: 0.5px;
    text-align: center;
    color: $white;
    background-color: $blue_04;
    border-radius: 10px;
    box-shadow: 5px 10px 20px transparentize($color: $blue_03, $amount: 0.8);
    margin: 75px 0 0 213px;  //382px
    padding: 15px;
    text-transform: capitalize;
    text-decoration: none;
    cursor: pointer;

    &:hover {
        background-color: $blue_02;
    }
}

.header__img {
    position: absolute;
    top: -5px;
    right: 23px;
}