.blog {
    @include container;
}

.blog__list {
    @include list-style;
}

.blog__popular {
    display: flex;
    flex-direction: column;
    margin-top: 57px;
    padding-bottom: 25px;
    border-bottom: 2px solid transparentize($color: $black, $amount: 0.8);
    // outline: 1px solid red;
}

.blog__articles {
    margin-top: 57px;
    margin-bottom: 101px;
}

.blog__articles-list {
    margin-top: 35px;
}

.blog__popular-list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: auto auto;
    row-gap: 33px;
    column-gap: 20px;
    margin-top: 36px;
}

.blog__popular-item-main {
    grid-column: 1/-1;
    grid-row: 1/2;
    display: grid;
    grid-template-columns: 1fr auto;
    grid-template-rows: auto auto auto;
    column-gap: 20px;

    .blog__item-link-bookmarks,
    .blog__item-link-share {
        margin-top: 25px;
    }
}

.blog__articles-item {
    display: grid;
    grid-template-columns: auto 1fr;
    grid-template-rows: auto auto auto;
    column-gap: 20px;
    margin-top: 32px;
    // outline: 1px solid red;

    .blog__item-img {
        grid-column: 1/2;
        grid-row: 1/-1;
    }

    .blog__item-heading {
        margin: 0;
    }

    .blog__item-text {
        margin-left: 0;
        margin-top: 16px;
        padding-right: 85px;
    }
}

.blog__search {
    position: relative;
    // outline: 1px solid red;

    &-input {
        width: 100%;
        margin-top: 55px;
        padding: 20px 20px 18px 72px;
        background-color: $grey;
        border: none;
        border-radius: 10px;

        &::placeholder {
            font-size: 16px;
            line-height: 16px;
            font-weight: 200;
            color: transparentize($color: $blue_01, $amount: 0.4);
            text-transform: capitalize;
        }

        &:hover + .blog__search-icon{
            opacity: 0.7;
        }
    }

    &-icon {
        position: absolute;
        top: 0;
        left: 25px;
        opacity: 0.4;
    }
}

.blog__title {
    font-size: 20px;
    line-height: 100%;
    letter-spacing: 1px;
    margin: 0;
    color: $blue_03;
    text-transform: uppercase;
}

.blog__item-heading {
    font-family: "Time New Roman", serif;
    font-size: 26px;
    line-height: 38px;
    // letter-spacing: 0.1px;
    margin-top: 10px;
    color: $blue_01;

    &-main {
        font-size: 44px;
        line-height: 58px;
        margin: 0;
    }
}

.blog__item-img {
    max-width: 100%;
    height: auto;

    &-main {
        grid-column: 2/3;
        grid-row: 1/-1;
    }
}

.blog__item-text {
    font-size: 16px;
    line-height: 26px;
    letter-spacing: 0.6px;
    margin-top: -10px;
    margin-left: 4px;
    color: transparentize($color: $black, $amount: 0.4);

    &-main {
        line-height: 27px;
        margin-left: 0;
        margin-top: 12px;
    }
}

.blog__item-author {
    font-style: normal;
    font-size: 14px;
    line-height: 24px;
    letter-spacing: 0.5px;
    color: $blue_01;
    margin-bottom: 4px;
}

.blog__item-data {
    font-size: 14px;
    line-height: 24px;
    letter-spacing: 0.5px;
    color: transparentize($color: $blue_01, $amount: 0.4);
}

.blog__item-footer {
    display: flex;
    align-items: flex-end;

    &-column2 {
        align-self: center;
        margin-left: 100px;
    }
}

.blog__item-link-bookmarks {
    display: inline-block;
    margin-top: 5px;
}

.blog__item-link-share {
    display: inline-block;
    margin-left: 25px;
}

.blog__item-icon1,
.blog__item-icon2 {
    fill: $blue_03;

    &:hover {
        fill: $blue_01;
    }
}

.blog__popular-link {
    font-size: 16px;
    line-height: 16px;
    font-weight: 900;
    letter-spacing: 0.5px;
    color: $blue_03;
    margin-top: 55px;
    margin-left: auto;
    text-transform: uppercase;
    text-decoration: none;
    
    &:hover {
        color: $blue_04;  
    }
}

.blog__articles-buttons {
    display: flex;
    justify-content: space-between;
    margin-top: 56px;
}

.blog__button {
    position: relative;
    font-size: 16px;
    line-height: 16px;
    font-weight: 900;
    letter-spacing: 0.5px;
    color: transparentize($color: $blue_03, $amount: 0.2);
    background-color: transparent;
    border: none;
    text-transform: capitalize;
    cursor: pointer;

    &:hover {
        color: $blue_03;
    }

    &::before {
        content: "";
        position: absolute;
        top: 0;
        width: 16px;
        height: 16px;
        background-image: url("../images/arrow.svg");
        background-size: 12px 12px; 
        background-repeat: no-repeat;
    }

    &-next {
        padding-left: 35px;

        &::before {
            left: 0;
            transform: rotate(180deg);
        }
    }

    &-back {
        padding-right: 35px;

        &::before {
            top: 5px;
            right: 0;
        }
    }
}



