.contacts {
    @include container;
    margin-top: 57px;
    margin-bottom: -123px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
}

.contacts-info__list {
    @include list-style;
    padding-top: 315px;
    background-color: $white;
    background-image: url("../images/contacts-background.svg");
    background-repeat: no-repeat;
    background-position: 12px 63px;
    background-size: 300px 181px;
}

.contacts-info__item {
    display: flex;
    flex-direction: column;

    &-title {
        font-size: 20px;
        line-height: 30px;
        letter-spacing: 0.9px;
        color: $blue_01;
        margin: 0;
        text-transform: uppercase;
    }

    &-text {
        font-size: 16px;
        line-height: 26px;
        font-weight: 200;
        letter-spacing: 0.6px;
        color: $blue_01;
        padding-top: 8px;
        opacity: 0.6;
        text-decoration: none;
    }

    &-phone {
        margin-top: 25px;

        .contacts-info__item-text {
            padding-left: 40px;
            background-color: $white;
            background-image: url("../images/phone.svg");
            background-repeat: no-repeat;
            background-position: 0 10px;

            &:hover {
                opacity: 1;
            }

            &:first-of-type {
                margin-top: 16px;
            }

            &:last-of-type {
                margin-top: 10px;
            }
        }
    }

    &-email {
        margin-top: 40px;

        .contacts-info__item-text {
            padding-left: 40px;
            background-color: $white;
            background-repeat: no-repeat;
            background-position: 0 10px;

            &:hover {
                opacity: 1;
            }

            &:first-of-type {
                margin-top: 16px;
                letter-spacing: 0.8px;
                background-image: url("../images/web.svg");
            }

            &:last-of-type {
                margin-top: 9px;
                letter-spacing: 0.8px;
                background-image: url("../images/email.svg");
            }
        }
    }
}

.contacts-form {
    display: flex;
    flex-direction: column;
    width: 428px;
    margin-left: 89px;
    padding: 56px;
    border-radius: 10px;
    box-shadow: 5px 10px 50px transparentize($color: $blue_00, $amount: 0.8);

    &__heading {
        font-size: 20px;
        line-height: 30px;
        letter-spacing: 1px;
        color: $blue_01;
        margin: 0;
        text-transform: uppercase;
    }

    &__list {
        @include list-style;
        margin-top: 47px;
    }

    &__button {
        font-family: "Gilroy";
        font-size: 16px;
        line-height: 26px;
        font-weight: 900;
        letter-spacing: 0.7px;
        padding: 23px;
        margin-top: 72px;
        background: $blue_04;
        color: $white;
        border: none;
        border-radius: 10px;
        box-shadow: 5px 20px 20px transparentize($color: $blue_00, $amount: 0.8);
        text-transform: uppercase;
        cursor: pointer;

        &:hover {
            background-color: $blue_02;
        }
    }
}

.contacts-form__item {
    display: flex;
    flex-direction: column;

    &-name {
        font-size: 16px;
        line-height: 30px;
        letter-spacing: 0.8px;
        color: transparentize($color: $blue_01, $amount: 0.2);
        text-transform: capitalize;
    }

    &-text {
        margin-top: 10px;
        padding: 25px 40px;
        border: 1px solid transparentize($color: $blue_03, $amount: 0.6);
        border-radius: 10px;

        &:hover {
            border-color: $blue_03;
        }

        &::placeholder {
            font-family: "Gilroy";
            font-size: 16px;
            font-weight: 200;
            letter-spacing: 0.8px;
            color: transparentize($color: $blue_01, $amount: 0.8);
            text-transform: capitalize;
        }
    }

    &:nth-of-type(2) {
        margin-top: 31px;
    }

    &:last-of-type {
        margin-top: 30px;

        .contacts-form__item-text {
            min-height: 90px;
            padding-top: 25px;
            padding-left: 35px;
        }
    }
}

.contacts-map {
    height: 480px;
    margin-top: 98px;
    grid-column: 1/3;
}